@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
html{font-size:18px}*{margin:0;padding:0;box-sizing:border-box}body{font-family:sans-serif;line-height:1.4em;background-color:#BEA6A0;position:relative;min-height:100vh}a{color:black;text-decoration:none}

.navbar{width:100%;height:80px;background:#C34A36;color:#B0A8B9;align-items:center;display:flex;flex-direction:row;justify-content:center;align-items:center;box-shadow:0px 4px 5px -1px rgba(50,50,50,0.74);position:fixed;top:0;z-index:2}.navbar div{font-family:'Yellowtail', cursive;font-size:1.4em;display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:90%;margin:auto}.navbar div div{width:100%}

.main{width:90%;margin:0 auto;position:relative;display:flex;flex-direction:column;z-index:1}.main .mygrid{width:100%;margin:auto;display:flex;flex-direction:row;justify-content:space-around;align-items:none;flex-wrap:wrap}@media (max-width: 700px){.main .mygrid{display:flex;flex-direction:column;align-self:center;align-items:center}}

.block{position:relative;margin-bottom:15px;background:#845EC2;border-radius:10px;padding:20px;color:#B0A8B9;box-shadow:0px 4px 5px -1px rgba(50,50,50,0.74)}.block form{min-height:150px;display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch}.block form .deleteLine{margin-top:auto;align-self:flex-end}h1{font-family:'Yellowtail', cursive;font-weight:300px;padding-bottom:10px}

.myform{position:relative;width:100%;height:200px;margin-bottom:50px;margin-top:100px}.myform form{height:100%;max-width:700px;margin:auto;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;box-shadow:0px 4px 5px -1px rgba(50,50,50,0.74);border-radius:10px;background:#845EC2;position:relative}.myform form input{flex:0.5 1;border:none;background:none;width:90%;font-family:'Yellowtail', cursive;font-weight:700;font-size:50px}.myform form input:focus{outline:none}.myform form textarea{font-size:1.2rem;border:none;flex:2 1;background:none;width:90%}.myform form textarea:focus{outline:none}.myform form button{position:absolute;width:50px;height:50px;border-radius:50%;right:5%;bottom:-25px;background:#C34A36;color:#B0A8B9;box-shadow:0px 4px 5px -1px rgba(50,50,50,0.74);display:flex;flex-direction:column;justify-content:center;align-items:center}.myform form button:focus{outline:none}.myform form button:hover{background:#cc5947}

